export default {
    name: 'Home',
    data() {
        return {
            "basic": {
                "city": "北京",
            },
            "now": {
                "cond": {
                    "txt": ''
                }
            },
            "daily_forecast": [],
            "afterCalculater": {
                "maxTmps": [],
                "minTmps": [],
                "xDate": []
            }
        }
    },
    preFetch() {
        return this.methods.meta()
    },
    beforeMount() {
        this.$emit('view', this.meta())
    },
    created: function () {
        this.getWeathers();
        // console.log(this.$echarts);
    },
    methods: {
        meta: () => ({
            title: 'iWeAther',
            description: '当前天气',
            keywords: 'PWA'
        }),
        getWeathers: function () {
            let self = this
            self.$http.get('/now').then(function (response) {
                console.log(response.data.HeWeather5[0]);
                self.$data.now = response.data.HeWeather5[0].now;
                self.$data.basic = response.data.HeWeather5[0].basic;
            }).catch(function (error) {
                throw new Error(error);
            })

            self.$http.get('/forecast').then(function (response) {
                console.log(response.data.HeWeather5[0]);
                self.$data.daily_forecast = response.data.HeWeather5[0].daily_forecast;
                self.calcuTems(self.$data.daily_forecast);
                self.renderCharts();
            }).catch(function (error) {
                throw new Error(error);
            })
            console.log(self.$data)
        },
        calcuTems: function (temArr) {
            let [temMaxs, temMins, xDate] = [[], [], []];
            temArr.forEach((item, i) => {
                temMaxs.push(item.tmp.max);
                temMins.push(item.tmp.min);
                xDate.push(i == 0 ? '今天' : Number(item.date.split('-')[1]) + '-' + Number(item.date.split('-')[2]));

            })
            this.$data.afterCalculater = { maxTmps: temMaxs, minTmps: temMins, xDate: xDate };
            console.log(temMaxs, temMins);
        },
        renderCharts: function () {
            let self = this;
            let myChart = this.$echarts.init(document.getElementById('tem-week'));
            myChart.setOption({
                title: {
                    text: '未来一周天气状况'
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: self.$data.afterCalculater.xDate
                },
                yAxis: {
                    show: false,
                    type: 'value',
                    min: 'dataMin',
                    max: 'dataMin',
                    axisLabel: {
                        formatter: '{value} °C'
                    }
                },
                series: [
                    {
                        name: '最高气温',
                        type: 'line',
                        lineStyle: {
                            normal: {
                                // width: 10,
                                type: 'solid',
                                shadowColor: 'rgba(0, 0, 0, 0.5)',
                                shadowBlur: 10
                            }
                        },
                        smooth: true,
                        data: self.$data.afterCalculater.maxTmps,
                        // markPoint: {
                        //     data: [
                        //         { type: 'max', name: '最大值' },
                        //         { type: 'min', name: '最小值' }
                        //     ]
                        // },
                        markLine: {
                            data: [
                                { type: 'average', name: '平均值' }
                            ]
                        }
                    },
                    {
                        name: '最低气温',
                        type: 'line',
                        lineStyle: {
                            normal: {
                                // width: 10,
                                type: 'solid',
                                shadowColor: 'rgba(0, 0, 0, 0.5)',
                                shadowBlur: 10
                            }
                        },
                        smooth: true,
                        data: self.$data.afterCalculater.minTmps,
                        // markPoint: {
                        //     data: [
                        //         { name: '周最低', value: -2, xAxis: 1, yAxis: -1.5 }
                        //     ]
                        // },
                        markLine: {
                            data: [
                                { type: 'average', name: '平均值' },

                            ]
                        }
                    }
                ]
            });

            console.log(myChart)
        }

    }
}



